
/* Definições gerais do rodapé */
footer {
  background-image: url('../Main/images/techsWallpaper3.jpg'); 
  color: rgb(15, 4, 4);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  letter-spacing: 1px;
  font-family:sans-serif;
  height: 380px;
  text-rendering: optimizeLegibility;
}

/* container pai - Primeira camada */
.contato-container {
  border-radius: 10px;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  background-color: rgb(209, 196, 196);
  color: rgb(31, 26, 26);
  width: 36%;
  position: relative;
  padding: 20px;
  bottom: -5%;
  left: 10%;
  opacity: 80%;
  text-align: left;
  transition: 0.35s all ease-in-out;
}
.contato-container:hover{
  opacity: 70%;
  transition: 0.35s all ease-in-out;
}
h2{
  font-size:x-large;
  padding: 10px 0px 15px 10px;
}
.social-contact a{
  margin-left: 20px;
  color: rgb(31, 26, 26);
}
#linkedin-icon{
  background-color: #0982c0;
  opacity:100%;
}

#myLinkedin{
  color: rgb(31, 26, 26);
  text-decoration: none;
  padding: 10px 0px 10px 0px;
  transition: 0.35s all ease-in-out;
  text-rendering: optimizeLegibility;
  display:flex;
  align-items: center;
  grid-gap:6px;
  width: 390px;
}

#myLinkedin:hover{
  list-style: none;
  color: #0982c0;
  background-color: #fffefe;
  border-radius:10px;
  padding: 10px 10px 10px 10px;
  transition: 0.35s all ease-in-out;

}

#whatsapp-icon {
  background-color:rgb(16, 185, 30);
  border-radius: 50%;
  text-shadow: 0 0 25px #fffefe;
}

#myWhatsapp {
  color: rgb(31, 26, 26);
  font-family: "proxima nova light", "Helvetica Neue", Helvetica, Arial, Sans-serif;
  text-decoration: none;
  transition: 0.35s all ease-in-out;
  text-rendering: optimizeLegibility;
  display:flex;
  align-items: center;
  width: 390px;
  grid-gap:6px;
  padding: 10px 0px 10px 0px;
}
#myWhatsapp:hover {
  list-style: none;
  background-color: #fffefe;
  padding-left: 8px;
  border-radius:10px;
  color: #09c055;
  transition: 0.35s all ease-in-out;
}

.back-top {
  position: relative;
  bottom:-30%;
}
.back-top img{
  max-width: 50px;
  transition: 0.35s all ease-in-out;
}
.back-top a:hover{
  opacity:70%;
  transition: 0.35s all ease-in-out;
}

.transition{
  position: relative;
  bottom:-30%;
  text-decoration: none;
  color: rgb(243, 233, 233);
  transition: 0.35s all ease-in-out;
}
.transition a{
  color: rgb(39, 25, 25);
  transition: 0.35s all ease-in-out;
  text-decoration: none;
}

.transition p{
  transition: 0.35s all ease-in-out;
}
.transition p:hover {
  opacity: 0.4;
  color:rgb(241, 226, 226);
  background-color: rgb(14, 1, 1);
  transition: 0.35s all ease-in-out;
}

  @media screen and (max-width: 400px){
    footer {
      background-image: url('../Main/images/techsWallpaper3.jpg'); 
      color: whitesmoke;
      box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
      letter-spacing: 1px;
      font-family:sans-serif;
      width:100%;
      height: 480px;
      text-rendering: optimizeLegibility;
    }
      .social-contact {
        font-size: 9px;
      }

      .contato-container {
        width: 79%;
        opacity: 80%;
        text-align: left;
        margin: 0px 0px 0px 2px;
        transition: 0.35s all ease-in-out;
      }
      
  }

  /* Iphone 8*/
  @media screen and (max-width: 415px){
    footer {
      width: 472px;
      height: 480px;
      text-rendering: optimizeLegibility;
    }
      .contato-container {
        font-size:11px;
        width:80%;
        margin: 0px 0px 0px 2px;
      }
  }

  @media screen and (max-width: 600px){
    .contato-container {
      font-size:11px;
      width:80%;
      margin: 0px 0px 0px 2px;
    }
  }