.section-02 {
    margin: 0;
    padding: 0;
    background-color: #f1f1f1; opacity:1.2;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    font-family: fantasy;
    font-size: 20px;
    width:100%;
    height: 700px;
}
.about-container {
     padding-top: 100px;
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.about-me {
    border-radius: 20px;
    width: 40%;
    height:40%;
    justify-content:center;
    align-items: center;
    opacity: 0.8;
    font-weight: 400;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #282c34;
	  transition: 0.35s all ease-in-out;
}
.about-me h1{
    border-radius:10%;
    background-color: #282c34;
    font-size: 40px;
    color: whitesmoke;
}
.techs {
    display: flex;
    grid-gap: 20px;
    text-align: center;
    justify-content: left;
    margin-left: 43px;
    width: 17%;
    
}
.techs p{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    grid-gap: 10px;
    flex-direction: column;
    background-color: whitesmoke;
    font-size: 14px;
    font-weight: 200;
    border: 2px solid rgb(139, 207, 122);
    padding: 20px 30px 20px 30px ;
    transition: 0.35s all ease-in-out;
}
.techs p:hover{
    opacity:0.9;
    background-color: rgb(243, 235, 235);
    transition: 0.35s all ease-in-out;
}
.techs img{
    width: 70%;
}

.items-1{
  display: flex;
  grid-gap:20px;
}
.items-2{
  display: flex;
  grid-gap:20px;
}

.me #kakashi {
    width:330px;
    height:327px;
}

h3 {
    border-radius:10%;
    background-color: #282c34;
    font-weight: 700;
    color: whitesmoke;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

/*Seta para voltar pro topo da página. */

@keyframes mymove {
    0%   {bottom: 100px;}
  }

/* */
@keyframes pulse {
    0% {
         transform: scale(1); 
        }
    50% {
         transform: scale(1.1);
    }
    100% { transform: scale(1); }
    }



@media (prefers-reduced-motion: no-preference) {
   #React-Logo {
      animation: App-logo-spin infinite 7s linear;
    }
  }

    /*Iphone 6/7/8 Plus */
  @media screen and (max-width: 414px){
    .section-02{
      width: 472px;
      flex-direction: column;
      height: 100%;
      padding-bottom: 20px;
    }
    .about-container{
      height: 100%;
      width:100%;
      flex-direction:column;
    }
    .about-me {
      width: 90%;
      height: 100%;
  }
    .about-me h1{
      padding-top: -20px;
      display: flex;
      flex-direction:column;
      width: 400px;
      
      }
    .about-me p{
      width: 100%;
      margin-left: 5%;
      }
        
      .techs {
        padding: top 20px;
        flex-direction: column;
        width: 100%;
      }

      .techs p img{
      width: 38px;
      height: 38px;
      }
  }



  /* Iphone X */
  @media screen and (max-width: 400px){
    
    .section-02 {
        width: 472px; 
        height: 100%;
        flex-direction: column;
        justify-content:center;
        align-items: center;
        padding-bottom: 20px;
    }

    .about-container {
      flex-direction:column;
      padding-bottom: 20px;
      width:472px;
    }
   
    .about-me {
      height: 80%;
      width:472px;
    }
    .about-me h1{
      height: 80%;
      width:472px;
    }
    .about-me p{
      height: 80%;
      width:412px;
    }
    .techs {
      flex-direction: column;
      width: 472px;
      position: relative;
      left:5%;
    }
    
  .techs p img{
    width: 38px;
    height: 38px;
}

}

@media screen and (max-width: 600px){
  .section-02 {
    flex-direction: column;
    justify-content:center;
    align-items: center;
    height:1010px;
}


.about-container {
  align-items: center;
  justify-content: center;
  flex-direction: column;
  
}
.about-me {
  width:500px;
  
}

.techs {
  margin-top: 20px;
  flex-direction: column;
  width: 472px;
  position: relative;
  left:15%;
}
} 