.slide-project {
    margin: 0;
    padding: 0;
    background-color: #282c34;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    font-family: fantasy;
    font-size: 20px;
}
.slide-project h3{
    border: 0 auto;
    height:100px;
    padding-top: 50px;
    justify-content: center;
}
.SlideShow{
    padding-top: 100px;
    background-color: rgb(254, 251, 255);;
    display:flex;
    height: 650px;
    justify-content: center;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  }
  .SlideShow p{
    color: #282c34;
    font-size: 20px;
    opacity: 90%;
    padding:10px;
  }
  .SlideShow a{
    
    text-decoration: none;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    transition: 0.40s all ease-in-out;
  }
  .SlideShow a:hover{
    opacity: 0.6;
    cursor: pointer;
    transition: 0.40s all ease-in-out;
  }
  section h3 {
    font-weight: 500;
    color:whitesmoke;
    opacity: 90%;
    margin-top: -10px;
  }

  @media screen and (max-width: 400px){
      .slide-project {
        width:100%;
      }
    .SlideShow{
      padding-top: 100px;
      background-color: rgb(254, 251, 255);;
      display:flex;
      height: 550px;
      justify-content: center;
      box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    }
  }

  /*Iphone 6/7/8 Plus */
  @media screen and (max-width: 414px){
      .slide-project {
        width:472px;
      }
    .SlideShow{
      padding-top: 90px;
      background-color: rgb(254, 251, 255);;
      display:flex;
      height: 550px;
      justify-content: center;
      box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    }
  }

  @media screen and (max-width: 600px){
    .slide-project {
      height: 560px;
    }

  }