.App-header .flags {
    position:absolute;
    top: 1%;
    right: 1%;
    display: flex;    
}
.flags img{
    max-width:22px;
    border-radius:3px
}
.flags button {
    cursor: pointer;
    width: 30px;
    height: 30px;
    border:0.5px;
    background-color: #282c34;
    transition: 0.35s all ease-in-out;
}
.flags button:hover {
  opacity:80%;
  transition: 0.35s all ease-in-out;
}
#card h1{
    color: orangered;
}
#card {
    opacity:0.4;
    border-radius: 20px;
    padding-top: 40px;
    border: 2px solid;
    position: fixed;
    left: 100%;
    width: 150px;
    height: 120px;
    font-size: 10px;
    text-decoration: none;
    transition: 0.35s all ease-in-out;
}
#card:hover {
    opacity:1.2;
    transition: 0.35s all ease-in-out;
    background-color: #282c34;
}
nav {
    background-color: #282c34;
    padding: 50px;
    display:flex;
    justify-content: space-around;
    text-align: center;
    align-items: center;
    font-family:fantasy;
    border-radius: 110px 20px 20px 0px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

#logo {
    font-family:cursive;
    color: rgb(235, 229, 229);
    border-radius: 100%;
    opacity: 0.9;
    font-size: 65px;
    padding: 0px 10px 0px 10px;
    background-color:#0e1116;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    transition: 0.40s all ease-in-out;
}


#logo:hover {
    font-size: 65px;
    cursor: pointer;
    opacity: 0.7;
    font-family:cursive;
    background-color: rgb(235, 229, 229);
    border-radius: 100%;
    color: #282c34;
    -webkit-transition: 0.35s all ease-in-out;
	-moz-transition: 0.35s all ease-in-out;
	-o-transition: 0.35s all ease-in-out;
	transition: 0.65s all ease-in-out;
    animation: pulse 1s infinite;
    animation-timing-function: linear; 
}
nav a{
    text-decoration: none;
    cursor:pointer;
    transition: all 0.9s ease 0s;
    -webkit-transition: 0.40s all ease-in-out;
	-moz-transition: 0.40s all ease-in-out;
	-o-transition: 0.40s all ease-in-out;
	transition: 0.40s all ease-in-out;
}

.my-menu a{
    color: rgb(235, 229, 229);
    -webkit-transition: 0.40s all ease-in-out;
	-moz-transition: 0.40s all ease-in-out;
	-o-transition: 0.40s all ease-in-out;
	transition: 0.40s all ease-in-out;
}

.my-menu a:hover{
    color: rgb(255, 253, 253);
    opacity: 0.4;
    -webkit-transition: 0.40s all ease-in-out;
	-moz-transition: 0.40s all ease-in-out;
	-o-transition: 0.40s all ease-in-out;
	transition: 0.40s all ease-in-out;
}

nav ul {
    display: flex;
    margin-left: 290px;
    grid-gap: 20px;
    text-decoration: none;
    font-size: larger;
}

/* Redes sociais */

svg {
    opacity: 1.5;
    color: rgb(235, 228, 228);
    cursor: pointer;
    animation: linkedin 1s ease-in-out infinite alternate;
    -webkit-transition: 0.35s all ease-in-out;
	-moz-transition: 0.35s all ease-in-out;
	-o-transition: 0.35s all ease-in-out;
	transition: 0.35s all ease-in-out;
}

#linkedin:hover {
    background-color:#0072b1;
    border-radius: 50%;
    text-shadow: 0 0 25px #fffefe;
}
#github:hover {
    background-color:rgb(0, 0, 0);
    border-radius: 50%;
    text-shadow: 0 0 25px #fffefe;
}
#whatsapp:hover {
    background-color:rgb(16, 185, 30);
    border-radius: 50%;
    text-shadow: 0 0 25px #fffefe;
}


/* Iphone 6/7/8 Plus*/
@media screen and (max-width: 415px) {
    header {
        width:472px;
    }
    .App-header .flags {
        position:absolute;
        top: 1%;
        right: -10%;
        display: flex;    
    }
    #logo {
        position:absolute;
        top: 4%;
        left: 6%;
        width:55px;
        border-radius: 90%;
        font-size: 30px;
    }
    .my-menu {
        font-size: 11pt;
        position: absolute;
        top: 6%;
        right: 20%;
     }
     .social {
        position: absolute;
        top: 9%;
        right: 33%;
     }
     .social svg{
        width: 15px;
     }
     header .flags {
        position:absolute;
        top: 1%;
        right: 7%;
        display: flex;
    }
    .flags button {
       
        width: 17px;
        height: 17px;
       
    }
    .flags img{
        width:15px;
        border-radius:3px
    }

}


@media screen and (max-width: 600px) {
#logo {
   position:absolute;
   top: 1%;
   left: 1%;
}
}






 
    
    


