main {
    display:flex;
    padding-top: 30px;
    justify-content: space-evenly;
    background-color: #f1f1f1; opacity:1.2;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    /*background-image: url("./images/background.gif");*/
    width: 100%;
    height: 480px;
}
main ul{
  transition: 0.35s all ease-in-out;
}

main ul {
    padding-top: 70px;
}

main ul li{
  font-weight: 400;
  font-size: 3.4rem;
  font-family: fantasy;
  text-decoration: none;
  color: #241f1e;
}
main p{
    font-size: 15px;
    color: #241f1e;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
#myImage{
    opacity: 1.5;
	transition: 0.35s all ease-in-out;
    width:300px;
    border-radius: 50%;
    box-shadow: rgba(184, 171, 179, 0.4) 5px 5px, rgba(3, 3, 3, 0.726) 10px 10px, rgba(165, 148, 141, 0.507) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px, rgba(240, 46, 170, 0.05) 25px 25px;
}

@media screen and (max-width: 400px){
   
    main {
       flex-direction: column; 
       width:100%;
       height: 100%;
       background-color: rgb(185, 174, 174);
    }
    main ul {
        width: 75%;
        margin-left: 12%;
    }
    main li{
        margin-top: -55px;
    }
    main p{
       padding:0px 0px 20px 0px ;
    }

}
main {
    animation: fadeIn 3s;
    -webkit-animation: fadeIn 3s;
    -moz-animation: fadeIn 3s;
    -o-animation: fadeIn 3s;
    -ms-animation: fadeIn 3s;
  }
  @keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }

  /* Iphone 6/7/8 Plus */
  @media screen and (max-width: 414px) {
  main {
    flex-direction: column; 
    width:472px;
    height: 100%;
    background-color: rgb(185, 174, 174);
 }
 main ul {
     width: 75%;
     margin-left: 12%;
 }
 main li{
     margin-top: -55px;
 }
 main p{
    padding:0px 0px 20px 0px ;
 }
}

@media screen and (max-width: 601px) {
    main {
        flex-direction: column;
        height: 750px;
     }
     main ul {
        width: 75%;
        margin-left: 12%;
    }
    main li{
        margin-top: -125px;
    }

     main p{
        margin-top: -25px;
        padding:30px 0px 20px 0px ;
     }
     main a{
        margin-top: -70px;
     }
}